.header .slider {
  cursor: pointer;
  background: #7a7a7a;
  border-radius: 100%;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.header .slider:before {
  background-color: #fff;
  border-radius: 100%;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.header input:checked + .slider:before {
  content: "";
  background: url("moon.e6f90b43.svg") center / contain no-repeat;
  background-color: inherit;
  transform: translateX(20px);
}

.header .slider.round {
  border-radius: 34px;
}

